import {
  Switch,
  Route
} from 'react-router-dom'
import { useState } from 'react'

import Auth from './pages/Auth/Auth'

import {
  Category,
  Collection,
  Children,
  News,
  Mymovies,
  Subscriber,
  Home,
  Singlemovie
} from './pages'
import Header from './containers/Header'

import Search from './pages/Search/Search'

import './assets/styles/main.scss'
function App() {
  const [token, SetToken] = useState(window.localStorage.getItem('sessionToken'))
  return (
    
    <>
        {
          token ? (
            <>
              <Header/>
            <Switch>
                <Route exact path="/" component={Home}/>
                <Route exact path="/news" component={News}/>
                <Route exact path="/category" component={Category}/>
                <Route exact path="/collection" component={Collection}/>
                <Route exact path="/subscriber" component={Subscriber}/>
                <Route exact path="/mymovies" component={Mymovies}/>
                <Route exact path="/children" component={Children}/>
                <Route exact path="/search" component={Search}/>
                <Route exact path="/singlemovie/:id" component={Singlemovie}/>
            </Switch>
            </>
          ):(
            <Switch>
            <Route exact path="/" render={() => <Auth SetToken={SetToken} token={token}/>}/>
          </Switch>
          )
        }
    </>
  )
}

export default App;
